/* Doctor Registration Modal Styles */
.doctor-registration-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  animation: fadeIn 0.3s ease-in-out;
}

.doctor-registration-modal {
  background: white;
  border-radius: 15px;
  width: 90%;
  max-width: 700px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease-in-out;
}

.doctor-registration-header {
  background: linear-gradient(135deg, #007bff, #0056b3);
  color: white;
  padding: 1.5rem 2rem;
  border-radius: 15px 15px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.doctor-registration-header h2 {
  margin: 0;
  font-size: 1.5rem;
}

.close-btn {
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  padding: 0;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background 0.3s ease;
}

.close-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.doctor-registration-form {
  padding: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #007bff;
  font-size: 1rem;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.form-group select {
  cursor: pointer;
  background-color: white;
}

.file-input {
  border: 2px dashed #007bff !important;
  background: #f8f9ff;
  cursor: pointer;
  padding: 1rem !important;
  text-align: center;
}

.file-input:hover {
  background: #e6f0ff;
}

.file-help {
  display: block;
  margin-top: 0.5rem;
  color: #666;
  font-size: 0.85rem;
  font-style: italic;
}

/* Checkbox group styles */
.checkbox-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.checkbox-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem;
  border: 2px solid #ddd;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: normal;
  color: #333;
}

.checkbox-item:hover {
  border-color: #007bff;
  background: #f8f9ff;
}

.checkbox-item input[type="checkbox"] {
  width: auto;
  margin: 0;
  padding: 0;
}

.checkbox-item input[type="checkbox"]:checked + span {
  font-weight: 600;
  color: #007bff;
}

.checkbox-item:has(input:checked) {
  border-color: #007bff;
  background: #e6f0ff;
}

/* Time input styling */
.form-group input[type="time"] {
  font-family: monospace;
  font-size: 1.1rem;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid #eee;
}

.btn-cancel,
.btn-register {
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.btn-cancel {
  background: #6c757d;
  color: white;
}

.btn-cancel:hover {
  background: #545b62;
  transform: translateY(-1px);
}

.btn-register {
  background: #007bff;
  color: white;
}

.btn-register:hover {
  background: #0056b3;
  transform: translateY(-1px);
}

.login-prompt {
  text-align: center;
  padding-top: 1rem;
  border-top: 1px solid #eee;
  margin-top: 1rem;
}

.login-prompt p {
  margin: 0;
  color: #666;
  font-size: 0.9rem;
}

.login-link {
  background: none;
  border: none;
  color: #007bff;
  font-weight: 600;
  cursor: pointer;
  text-decoration: underline;
  font-size: 0.9rem;
  margin-left: 0.5rem;
  transition: color 0.3s ease;
}

.login-link:hover {
  color: #0056b3;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .doctor-registration-modal {
    width: 95%;
    margin: 1rem;
  }
  
  .doctor-registration-header {
    padding: 1rem 1.5rem;
  }
  
  .doctor-registration-form {
    padding: 1.5rem;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .btn-cancel,
  .btn-register {
    width: 100%;
  }
}

/* Form validation styles */
.form-group input:invalid,
.form-group textarea:invalid,
.form-group select:invalid {
  border-color: #dc3545;
}

.form-group input:valid,
.form-group textarea:valid,
.form-group select:valid {
  border-color: #007bff;
}

/* Special styling for required indicators */
.form-group label::after {
  content: ' *';
  color: #dc3545;
  font-weight: bold;
}
